import React from 'react';
import Latex from 'react-latex';
import Divider from '@material-ui/core/Divider';
import Box from '@material-ui/core/Box';
export default function NormasMatrizesVetores(){ return(<>

<h2>
    Normas de Vetores
</h2>


<h3>
    Definição de Norma de Vetor
</h3>

A norma de um vetor em <Latex>{"$\\mathbb{R}^n$"}</Latex> é definida 
por uma função <Latex>{"$||.||_{v}\\rightarrow\\mathbb{R}$"}</Latex> que satisfaz as seguintes propriedades:

<ul>
    <li>
        <b>Propriedade I : </b> <Latex>{"$||\\vec{x}||\\geq 0,\\forall \\vec{x}\\in\\mathbb{R}^n$"}</Latex>, 
        e <Latex>{"$||\\vec{x}||=0$"}</Latex> se e somente se <Latex>{"$\\vec{x}=\\vec{0}$"}</Latex>
    </li>
    <li>
        <b>Propriedade II : </b> <Latex>{"$||\\alpha\\vec{x}||=|\\alpha|.||\\vec{x}||$"}</Latex>, <Latex>{"$\\forall\\alpha\\in\\mathbb{R}$"}</Latex>, <Latex>{"$\\forall\\vec{x}\\in\\mathbb{R}^n$"}</Latex>
    </li>
    <li>
        <b>Propriedade III : </b><Latex>{"$||\\vec{x}+\\vec{y}||\\leq||\\vec{x}||+||\\vec{y}||$"}</Latex>, <Latex>{"$\\forall\\vec{x},\\vec{y}\\in\\mathbb{R}^n$"}</Latex>
    </li>
</ul>


<h3>
    Tipos de Normas
</h3>

<ul>
    <li>
        Norma-1 : <Latex>{"$||\\vec{x}||_{1}=\\sum^{n}_{i=0}|x_i|$"}</Latex>
        <ul>
            <li>
                <Box m={2}>
                    <b>Propriedade I</b> : 
                    <Latex displayMode={true}>{'$$'+
                    '\\text{Já que}\\mid x\\mid\\geq 0,\\;\\forall x\\in\\mathbb{R}\\text{, então }\\sum^{n}_{i=0}\\mid x_i\\mid \\geq 0\\text{, e  }\\sum^{n}_{i=0}\\mid x_i\\mid =0\\Leftrightarrow\\vec{x}=\\vec{0}'
                    +'$$'}</Latex>
                </Box>
            </li>
            <li>
                <Box m={2}>
                    <b>Propriedade II:</b>
                    <Latex displayMode={true}>{'$$'+
                    '|| \\alpha\\vec{x} || = \\sum^{n}_{i=0}\\mid \\alpha x_i\\mid = \\mid\\alpha\\mid \\sum^{n}_{i=0}\\mid x_i\\mid = \\mid\\alpha\\mid||\\vec{x}||'
                    +'$$'}</Latex>
                </Box>
            </li>
            <li>
                <Box m={2}>
                    <b>Propriedade III:</b>
                    <Latex displayMode={true}>{'$$'+
                    '|| \\vec{x} + \\vec{y} || = \\sum^{n}_{i=0}\\mid x_i +y_i\\mid \\leq \\sum^{n}_{i=0}(\\mid x_i\\mid+\\mid y_i\\mid) =  \\sum^{n}_{i=0}\\mid x_i\\mid + \\sum^{n}_{i=0}\\mid y_i\\mid = || \\vec{x}||  + || \\vec{y} ||'
                    +'$$'}</Latex>
                </Box>
            </li>
        </ul>
    </li>
    <li>
        Norma-2 (Euclideana) : <Latex>{"$||\\vec{x}||_{2}=\\sqrt{\\sum^{n}_{i=0}|x_i|^2}$"}</Latex>
    </li>
    <li>
        Norma-p : <Latex>{"$||\\vec{x}||_{p}=\\sqrt[p]{\\sum^{n}_{i=0}|x_i|^p}$"}</Latex>
    </li>
    <li>
        Norma infinita : <Latex>{"$||\\vec{x}||_{\\infty}=\\max_{1\\leq i\\leq n}\{x_i\}$"}</Latex>
        <ul>
            <li>
                <Box m={2}>
                    <b>Propriedade I</b> : 
                    <Latex displayMode={true}>{'$$'+
                    '\\text{Já que }\\mid x \\mid\\geq 0,\\;\\forall x\\in R\\text{, então }\\max_{1\\leq i \\leq n}\\mid x_i\\mid \\geq 0\\text{, e  }\\max_{1\\leq i \\leq n}\\mid x_i\\mid =0\\text{ se e somente se }\\vec{x}=\\vec{0}'
                    +'$$'}</Latex>
                </Box>
            </li>
            <li>
                <Box m={2}>
                    <b>Propriedade II:</b>
                    <Latex displayMode={true}>{'$$'+
                    '||\\alpha\\vec{x}|| =\\max_{1\\leq i \\leq n}\\mid \\alpha x_i\\mid = \\mid\\alpha\\mid \\max_{1\\leq i \\leq n}\\mid x_i\\mid = \\mid\\alpha\\mid ||\\vec{x}||'
                    +'$$'}</Latex>
                </Box>
            </li>
            <li>
                <Box m={2}>
                    <b>Propriedade III :</b>
                    <Latex displayMode={true}>{'$$'+
                    '||\\vec{x}+\\vec{y}|| = \\max_{1\\leq i \\leq n}\\mid x_i + y_i\\mid \\leq \\max_{1\\leq i \\leq n}\\mid x_i \\mid + \\mid y_i\\mid \\leq \\max_{1\\leq i \\leq n}\\mid x_i\\mid + \\max_{1\\leq i \\leq n}\\mid y_i\\mid = ||\\vec{x}|| + ||\\vec{y}||'
                    +'$$'}</Latex>
                </Box>
            </li>
        </ul>
    </li>
</ul>

<Divider />

<h2>
    Normas de Matrizes
</h2>


<h3>
    Definição de Norma de Matriz
</h3>

A norma de uma matriz em <Latex>{"$\\mathbb{R}^{n\\times n}$"}</Latex> é definida  por uma função <Latex>{"$||.||\\rightarrow\\mathbb{R}$"}</Latex> que satisfaz as seguintes propriedades:

<ul>
    <li>
        <Latex>{"$||A||> 0$"}</Latex> se <Latex>{"$A\\mathrel{\\char`≠}\\mathbf{0}$"}</Latex> e <Latex>{"$||A||= 0$"}</Latex> se e somente se <Latex>{"$A=\\mathbf{0}$"}</Latex>
    </li>
    <li>
        <Latex>{"$||\\alpha A||=|\\alpha|.||A||$"}</Latex>, <Latex>{"$\\forall\\alpha\\in\\mathbb{R}$"}</Latex>, <Latex>{"$\\forall\\mathbf{x}\\in\\mathbb{R}^n$"}</Latex>
    </li>
    <li>
        <Latex>{"$||A+B||\\leq||A||+||B||$"}</Latex>
    </li>
    <li>
        <Latex>{"$||AB||\\leq||A||.||B||$"}</Latex>
    </li>
</ul>


<h3>
    Tipos de Normas 
</h3>

<ul>
    <li>
        Norma de Frobenius : <Latex>{"$||A||_{f}=\\sqrt{\\sum^{n}_{i=0}\\sum^{n}_{i=0}|x_{ij}|^2}$"}</Latex>
    </li>
    <li>
        Norma Induzida: <Latex>{"$||A||_{v} ||A||_v=\\max_{\\vec{x}\\mathrel{\\char`≠}\\vec{0}}\\frac{||A\\vec{x}||_v}{||\\vec{x}||_v}$"}</Latex>
        <ul>
            <li>
                <Box m={2}>
                    <b>Propriedade I</b> : 
                    <Latex displayMode={true}>{'$$'+
                    '\\text{Já que}||A\\vec{x}|| \\geq 0\\text{ e }||\\vec{x}|| \\geq 0\\text{ então }||A||_v = 0\\text{  se e somente se }A = \\mathbf{0} e ||A||_v > 0 \\;\\; \\forall\\vec{x}\\in R^n,\\vec{x}\\ne\\vec{0} ,\\forall A\\in R^{nxn}, A\\mathrel{\\char`≠}\\mathbf{0}'
                    +'$$'}</Latex>
                </Box>
            </li>
            <li>
                <Box m={2}>
                    <b>Propriedade II:</b>
                    <Latex displayMode={true}>{'$$'+
                    '||cA||_v=\\max_{\\vec{x}\\mathrel{\\char`≠}\\vec{0}}\\frac{||cA\\vec{x}||_v}{||\\vec{x}||_v}=\\max_{\\vec{x}\\mathrel{\\char`≠}\\vec{0}}\\frac{\\mid c \\mid||A\\vec{x}||_v}{||\\vec{x}||_v}=\\mid c \\mid \\max_{\\vec{x}\\mathrel{\\char`≠}\\vec{0}}\\frac{||A\\vec{x}||_v}{||\\vec{x}||_v}=\\mid c \\mid ||A||_v'
                    +'$$'}</Latex>
                </Box>
            </li>
            <li>
                <Box m={2}>
                    <b>Propriedade III :</b>
                    <Latex displayMode={true}>{'$$'+
                    '||A+B||_v=\\max_{\\vec{x}\\mathrel{\\char`≠}\\vec{0}}\\frac{||(A+B)\\vec{x}||_v}{||\\vec{x}||_v}=\\max_{\\vec{x}\\mathrel{\\char`≠}\\vec{0}}\\frac{||A\\vec{x}+B\\vec{x}||_v}{||\\vec{x}||_v}\\leq\\max_{\\vec{x}\\mathrel{\\char`≠}\\vec{0}}\\frac{||A\\vec{x}||+||B\\vec{x}||_v}{||\\vec{x}||_v}='
                    +'$$'}</Latex>
                    <Latex displayMode={true}>{'$$'+
                    '\\max_{\\vec{x}\\mathrel{\\char`≠}\\vec{0}}\\frac{||A\\vec{x}||_v}{||\\vec{x}||_v}+\\frac{||B\\vec{x}||_v}{||\\vec{x}||_v}\\leq\\max_{\\vec{x}\\mathrel{\\char`≠}\\vec{0}}\\frac{||A\\vec{x}||_v}{||\\vec{x}||_v}+\\max_{\\vec{x}\\mathrel{\\char`≠}\\vec{0}}\\frac{||B\\vec{x}||_v}{||\\vec{x}||_v} = ||A||_v + ||B||_v'
                    +'$$'}</Latex>
                </Box>
            </li>
            <li>
                <Box m={2}>
                    <b>Propriedade IV</b>
                    <Latex displayMode={true}>{'$$'+
                    '||AB||_v=\\max_{\\vec{x}\\neq\\vec{0}}\\frac{||(AB)\\vec{x}||_v}{||\\vec{x}||_v}\\leq\\max_{\\vec{x}\\neq\\vec{0}}\\frac{||A||_v||B||_v||\\vec{x}||_v}{||\\vec{x}||_v}=||A||_v||B||_v'
                    +'$$'}</Latex>
                </Box>
            </li>
            <li>
                <b>Teorema : </b> <Latex>{"$||A\\vec{x}||_v\\leq||A||_v\\;||\\vec{x}||_v$"}</Latex>, pois
        
                <Latex displayMode={true}>{'$$'+
                '\\frac{||A\\vec{x}||_v}{||\\vec{x}||_v} \\leq \\max_{\\vec{y} \\mathrel{\\char`≠} \\vec{0}}\\frac{||A\\vec{y}||_v}{||\\vec{y}||_v}=||A||_v \\Leftrightarrow '+
                '||A\\vec{x}||_v\\leq||A||_v\\;||\\vec{x}||_v'
                +'$$'}</Latex>
            </li>
        </ul>
    </li>
</ul>

</>)}